









































































import {mapState} from "vuex";
import {CHECK_AUTH} from "@/store/actions.type";
import Vue from 'vue';

export default Vue.extend({
    name: "Home",

    created() {
        this.$store
            .dispatch(CHECK_AUTH)
            .then(() => {});
    },

    computed: {
        ...mapState({
            user: (state: any) => state.auth.user
        }),
        isAuthenticated() { return this.user && JSON.stringify(this.user).length > 2; }
    },
});
