



























































































































import Vue from 'vue';
import {ADD_ITEM, DELETE_ITEM, GET_ITEMS, UPDATE_ITEM} from "@/store/actions.type";
import {BASE_URL} from '@/common/config';

export default Vue.extend<any, any, any, any>({
    name: "RedactionTable",

    props: ['active-page'],

    data() {
        return {
            baseUrl: BASE_URL,
            dialog: false,
            headers: {
                examinations: [
                    {
                        text: 'ID',
                        align: 'left',
                        value: 'id',
                    },
                    { text: 'Название', value: 'title' },
                    { text: 'Действия', value: 'action', sortable: false }
                ],
                subjects: [
                    {
                        text: 'ID',
                        align: 'left',
                        value: 'id',
                    },
                    { text: 'Экзамен', value: 'examination_id' },
                    { text: 'Название', value: 'title' },
                    { text: 'Действия', value: 'action', sortable: false }
                ],
                levels: [
                    {
                        text: 'ID',
                        align: 'left',
                        value: 'id',
                    },
                    { text: 'Предмет', value: 'subject_id' },
                    { text: 'Название', value: 'title' },
                    { text: 'Номер', value: 'number' },
                    { text: 'Действия', value: 'action', sortable: false }
                ],
                tasks: [
                    {
                        text: 'ID',
                        align: 'left',
                        value: 'id',
                    },
                    { text: 'Предмет', value: 'level_id' },
                    { text: 'Название', value: 'title' },
                    { text: 'Текст', value: 'text' },
                    { text: 'Действия', value: 'action', sortable: false }
                ]
            },
            items: [],
            subItems: [],
            editedIndex: -1,
            editedItem: {
                title: '',
                examination_id: 0,
                subject_id: 0,
                level_id: 0,
                text: '',
                number: '',
                image: null,
                answer: ''
            },
            defaultItem: {
                title: '',
                examination_id: 0,
                subject_id: 0,
                level_id: 0,
                text: '',
                number: '',
                image: null,
                answer: ''
            },
        };
    },

    computed: {
        formTitle () {
            return this.editedIndex === -1 ? 'Добавить' : 'Изменить'
        },
    },

    watch: {
        dialog (val) {
            val || this.close()
        },

        activePage() {
            this.initialize();
        },

    },

    created () {
        this.initialize();
    },

    methods: {
        initialize () {
            this.items = [];
            this.subItems = [];

            this.$store
                .dispatch(GET_ITEMS, {url: this.activePage})
                .then((data: []) => this.items = data);

            let subUrl = '';
            switch (this.activePage) {
                case 'subjects':
                    subUrl = 'examinations';
                    break;
                case 'levels':
                    subUrl = 'subjects';
                    break;
                case 'tasks':
                    subUrl = 'levels';
                    break;
            }
            if(subUrl) {
                this.$store
                    .dispatch(GET_ITEMS, {url: subUrl})
                    .then((data: []) => this.subItems = data);
            }
        },

        editItem (item: any) {
            this.editedIndex = this.items.indexOf(item);
            this.editedItem = Object.assign({}, item);
            this.dialog = true
        },

        deleteItem (item: any) {
            const index = this.items.indexOf(item);
            confirm('Вы действительно хотите удалить этот элемент?') &&
            this.$store
                .dispatch(DELETE_ITEM, {url: this.activePage, id: item.id})
                .then(() => this.items.splice(index, 1));
        },

        close () {
            this.dialog = false;
            setTimeout(() => {
                this.editedItem = Object.assign({}, this.defaultItem);
                this.editedIndex = -1
            }, 300)
        },

        save () {
            const fd = new FormData();
            for(const key of Object.keys(this.editedItem)) {
                if(key === 'image') {
                    if(typeof this.editedItem[key] === 'object' && this.editedItem[key]) {
                        fd.append(key, this.editedItem[key]);
                    }
                } else {
                    fd.append(key, this.editedItem[key]);
                }
            }

            if (this.editedIndex > -1) {
                this.$store
                    .dispatch(UPDATE_ITEM, {url: this.activePage, id: this.editedItem.id, body: fd})
                    .then((data: any) => {
                        Object.assign(this.items[this.editedIndex], data);
                        this.close();
                    });
            } else {
                this.$store
                    .dispatch(ADD_ITEM, {url: this.activePage, body: fd})
                    .then((data: any) => {
                        this.items.push(data);
                        this.close();
                    });
            }
        },

        // uploadImage(file: any) {
        //     this.editedItem.image = file;
        // }
    },
});
